<template>
  <div class="diary">
    <p class="title">Diary</p>
    <hr>
    <div class="input-group w-25">
      <input type="file" @change="upFile" class="form-control"  v-if="view">
    </div>
    <div class="input-group w-25 mt-3">
      <div class="input-group-append">
        <span class="input-group-text">開始日 例：2021-01</span>
      </div>
      <input type="tel" class="form-control" v-model="startDate">
    </div>

    <dl class="mt-4">
      <dt class="mb-2">手順</dt>
      <dd>1. <a href="https://takeout.google.com/" target="_blank"
          rel="noopener noreferrer">リンク</a>よりアカウントが正しいことを確認後「選択をすべて解除」、「GoogleChat」のみを選択</dd>
      <dd>2. 当サイトのファイルを選択から「messages.json」を選択</dd>
      <dd>3. クリップボードにコピーされる</dd>
    </dl>

    <dl class="mt-4">
      <dt class="mb-2">出力</dt>
      <dd v-html="output"></dd>
    </dl>
  </div>
</template>

<script>
export default {
  name: 'Diary',
  data() {
    return {
      startDate: '',
      output:'',
      view: true,
    };
  },
  mounted() {
     const today = new Date();
     this.startDate = today.getFullYear() + '-';
  },
  methods: {
    upFile(e) {
      const file = e.target.files[0];
      const reader = new FileReader();
      const dayStr = ['日', '月', '火', '水', '木', '金', '土'];
      reader.onload = e => {
        const data = JSON.parse(e.target.result)['messages'];
        console.log(data);
        let res = '';
        for (const row of data) {
          const dateStr = row.created_date.replace(new RegExp(/.曜日/, 'g'), '');
          const date = new Date(dateStr.replace('年', '/').replace('月', '/').replace('日', ' '));
          const startDate = new Date(this.startDate);
          if (date < startDate) continue;

          res += date.getFullYear() + '/' + ("0" + (date.getMonth() + 1)).slice(-2) + '/' + ("0" + date.getDate()).slice(-2) + '(' + dayStr[date.getDay()] + ') ' + date.getHours().toString().padStart(2, '0') + ':' + date.getMinutes().toString().padStart(2, '0') + '\n';
          res += row.text + '\n\n';
        }
        navigator.clipboard.writeText(res);
        this.output = res.replace(new RegExp(/\n/, 'g'), '<br>');
      }
      reader.readAsText(file);
      this.reset();
    },
    reset () {
      this.view = false
      this.$nextTick(function () {
        this.view = true
      })
    },
  },
}
</script>

<style scoped>
.title{
  font-size: 2rem;
  font-weight: 100;
}
</style>
